<template>
    <div ref="seeksRelationGraph" :style="{ width: '100%', height: '100%' }" style="box-sizing: border-box" @resize="refreshNVAnalysisInfo()">
        <GraphSettingPanel v-if="graphSettingWrap.graphSetting.allowShowSettingPanel" :graph-setting="graphSettingWrap.graphSetting">
            <!--  -->
            <!-- <template v-slot:settingPanelPlus>
                <slot :setting="setting" name="settingPanelPlus" />
            </template> -->
        </GraphSettingPanel>
        <graph-mini-name-filter v-show="graphSettingWrap.graphSetting.allowShowMiniNameFilter === true" :graph-setting="graphSettingWrap.graphSetting" />
        <graph-mini-tool-bar v-show="graphSettingWrap.graphSetting.allowShowMiniToolBar === true" :graph-setting="graphSettingWrap.graphSetting" />
        <graph-mini-view v-if="graphSettingWrap.graphSetting.allowShowMiniView === true" :graph-setting="graphSettingWrap.graphSetting" />
        <slot :graph="this" name="graphPlug" />
        <div
            :style="{ width: '100%', height: '100%', 'background-image': 'url(' + graphSettingWrap.graphSetting.backgrounImage + ')' }"
            :class="[graphSettingWrap.graphSetting.layoutClassName, graphSettingWrap.graphSetting.backgrounImageNoRepeat ? 'rel-map-background-norepeat' : '']"
            class="rel-map"
            @mousedown.left.stop="onDragStart($event)"
            @mousewheel="mouseListener"
        >
            <div ref="seeksRGCanvas" :style="canvasSizeAndPosition" class="rel-map-canvas">
                <div class="rel-nodediv rel-nodediv-for-webkit">
                    <SeeksRGNode v-for="thisNode in nodeViewList" :key="thisNode.seeks_id" :node-props="thisNode" :on-node-click="onRGNodeClick" :graph-setting="graphSettingWrap.graphSetting">
                        <template v-slot:node>
                            <slot :node="thisNode" name="node" />
                        </template>
                    </SeeksRGNode>
                </div>
                <div ref="rgCanvas" class="rel-linediv" style="overflow: visible">
                    <svg
                        :style="{ width: graphSettingWrap.graphSetting.canvasSize.width + 'px', height: graphSettingWrap.graphSetting.canvasSize.height + 'px' }"
                        style="overflow: visible"
                        xmlns="http://www.w3.org/2000/svg"
                        version="1.1"
                    >
                        <defs>
                            <linearGradient :id="graphSettingWrap.graphSetting.instanceId + '-lineStyle'" x1="1" y1="0" x2="0" y2="0">
                                <stop offset="0%" stop-color="#e52c5c" />
                                <stop offset="100%" stop-color="#FD8B37" />
                            </linearGradient>
                            <marker
                                :id="graphSettingWrap.graphSetting.instanceId + '-arrow-default'"
                                :markerWidth="graphSettingWrap.graphSetting.defaultLineMarker.markerWidth"
                                :markerHeight="graphSettingWrap.graphSetting.defaultLineMarker.markerHeight"
                                :refX="graphSettingWrap.graphSetting.defaultLineMarker.refX"
                                :refY="graphSettingWrap.graphSetting.defaultLineMarker.refY"
                                marker-units="strokeWidth"
                                orient="auto"
                                viewBox="0 0 12 12"
                            >
                                <path :style="{ fill: graphSettingWrap.graphSetting.defaultLineColor }" :d="graphSettingWrap.graphSetting.defaultLineMarker.data" />
                            </marker>
                            <marker
                                :id="graphSettingWrap.graphSetting.instanceId + '-arrow-checked'"
                                markerUnits="strokeWidth"
                                markerWidth="12"
                                markerHeight="12"
                                viewBox="0 0 12 12"
                                refX="6"
                                refY="6"
                                orient="auto"
                            >
                                <path :style="{ fill: '#FD8B37' }" d="M2,2 L10,6 L2,10 L6,6 L2,2" />
                            </marker>
                            <marker
                                v-for="thisColor in allLineColors"
                                :id="graphSettingWrap.graphSetting.instanceId + '-arrow-' + thisColor.id"
                                :key="thisColor.id"
                                :markerWidth="graphSettingWrap.graphSetting.defaultLineMarker.markerWidth"
                                :markerHeight="graphSettingWrap.graphSetting.defaultLineMarker.markerHeight"
                                :refX="graphSettingWrap.graphSetting.defaultLineMarker.refX"
                                :refY="graphSettingWrap.graphSetting.defaultLineMarker.refY"
                                marker-units="strokeWidth"
                                orient="auto"
                            >
                                <path :fill="graphSettingWrap.graphSetting.defaultLineMarker.color || thisColor.color" :d="graphSettingWrap.graphSetting.defaultLineMarker.data" />
                            </marker>
                        </defs>
                        <SeeksRGLink v-for="thisLine in lineViewList" :key="thisLine.seeks_id" :line-props="thisLine" :graph-setting="graphSettingWrap.graphSetting" :on-line-click="onRGLineClick" />
                    </svg>
                </div>
            </div>
        </div>

        <graph-bottom-panel v-if="$slots.bottomPanel" :graph-setting="graphSettingWrap.graphSetting">
            <template v-slot:bottomPanel>
                <slot :graph="this" name="bottomPanel" />
            </template>
        </graph-bottom-panel>

        <div style="clear: both; height: 1px" />

        <div
            v-if="isShowZoomCenter"
            :style="{ left: debugPanelPosition ? '' : '0px', right: debugPanelPosition ? '0px' : '' }"
            style="position: fixed; top: 0px; right: 0px; font-size: 12px; background-color: #333333; color: #ffffff; z-index: 9999; padding: 20px"
            @click="moveDebugPanel"
        >
            <div>
                <pre>{{ JSON.stringify(graphSettingWrap.graphSetting.canvasOffset, null, 2) }}</pre>
            </div>
            <div>
                <pre>{{ JSON.stringify(graphSettingWrap.graphSetting.viewNVInfo, null, 2) }}</pre>
            </div>
            <div>
                <pre>{{ JSON.stringify(graphSettingWrap.graphSetting.canvasNVInfo, null, 2) }}</pre>
            </div>
        </div>

        <div
            v-if="isShowZoomCenter"
            :style="{ left: graphSettingWrap.graphSetting.canvasNVInfo.x + graphSettingWrap.graphSetting.viewNVInfo.x + 'px', top: graphSettingWrap.graphSetting.viewNVInfo.y + 'px' }"
            style="position: fixed; top: 0px; width: 2px; height: 2000px; z-index: 99999; border-left: blue dotted 2px; opacity: 0.6"
        />
        <div
            v-if="isShowZoomCenter"
            :style="{ top: graphSettingWrap.graphSetting.canvasNVInfo.y + graphSettingWrap.graphSetting.viewNVInfo.y + 'px', left: graphSettingWrap.graphSetting.viewNVInfo.x + 'px' }"
            style="position: fixed; left: 0px; height: 2px; width: 2000px; z-index: 99999; border-top: blue dotted 2px; opacity: 0.6"
        />
        <div
            v-if="isShowZoomCenter"
            :style="{
                left: graphSettingWrap.graphSetting.canvasNVInfo.x + graphSettingWrap.graphSetting.canvasNVInfo.width / 2 + graphSettingWrap.graphSetting.viewNVInfo.x + 'px',
                top: graphSettingWrap.graphSetting.viewNVInfo.y + 'px'
            }"
            style="position: fixed; top: 0px; width: 4px; height: 2000px; z-index: 99999; border-left: green dotted 2px; opacity: 0.4"
        />
        <div
            v-if="isShowZoomCenter"
            :style="{
                top: graphSettingWrap.graphSetting.canvasNVInfo.y + graphSettingWrap.graphSetting.canvasNVInfo.height / 2 + graphSettingWrap.graphSetting.viewNVInfo.y + 'px',
                left: graphSettingWrap.graphSetting.viewNVInfo.x + 'px'
            }"
            style="position: fixed; left: 0px; height: 4px; width: 2000px; z-index: 99999; border-top: green dotted 2px; opacity: 0.4"
        />
        <div
            v-if="isShowZoomCenter"
            :style="{
                left: graphSettingWrap.graphSetting.canvasNVInfo.x + graphSettingWrap.graphSetting.canvasNVInfo.width + graphSettingWrap.graphSetting.viewNVInfo.x + 'px',
                top: graphSettingWrap.graphSetting.viewNVInfo.y + 'px'
            }"
            style="position: fixed; top: 0px; width: 2px; height: 2000px; z-index: 99999; border-left: blue dotted 2px; opacity: 0.6"
        />
        <div
            v-if="isShowZoomCenter"
            :style="{
                top: graphSettingWrap.graphSetting.canvasNVInfo.y + graphSettingWrap.graphSetting.canvasNVInfo.height + graphSettingWrap.graphSetting.viewNVInfo.y + 'px',
                left: graphSettingWrap.graphSetting.viewNVInfo.x + 'px'
            }"
            style="position: fixed; left: 0px; height: 2px; width: 2000px; z-index: 99999; border-top: blue dotted 2px; opacity: 0.6"
        />
    </div>
</template>

<script>
import { defineComponent, ref, reactive, computed, watch, onMounted, onBeforeUnmount, nextTick, provide } from 'vue';
import screenfull from 'screenfull';
import html2canvas from 'html2canvas';

import './core4vue/SeeksGraphIconfont';
import SeeksRGLayouters from './core4vue/SeeksRGLayouters';
import SeeksRGUtils from './core4vue/SeeksRGUtils';
import SeeksRGStore from './core4vue/SeeksRGStore';
import SeeksRGNode from './core4vue/SeeksRGNode';
import SeeksRGLink from './core4vue/SeeksRGLink';
import GraphSettingPanel from './GraphSettingPanel';
import GraphMiniView from './GraphMiniView';
import GraphMiniToolBar from './GraphMiniToolBar';
import GraphMiniNameFilter from './GraphMiniNameFilter';
import GraphBottomPanel from './GraphBottomPanel';

export default defineComponent({
    name: 'SeeksRelationGraph',
    components: { GraphBottomPanel, GraphMiniNameFilter, GraphMiniToolBar, GraphMiniView, SeeksRGNode, SeeksRGLink, GraphSettingPanel },
    props: {
        options: {
            mustUseProp: false,
            default: () => {
                return {};
            },
            type: Object
        },
        onNodeClick: {
            mustUseProp: false,
            default: () => {
                return () => {};
            },
            type: Function
        },
        onNodeExpand: {
            mustUseProp: false,
            default: () => {
                return () => {};
            },
            type: Function
        },
        onNodeCollapse: {
            mustUseProp: false,
            default: () => {
                return () => {};
            },
            type: Function
        },
        onLineClick: {
            mustUseProp: false,
            default: () => {
                return () => {};
            },
            type: Function
        },
        onDownloadExcel: {
            mustUseProp: false,
            default: null,
            type: Function
        },
        beforeDownloadImage: {
            mustUseProp: false,
            default: null,
            type: Function
        }
    },
    setup(props, { expose }) {
        // 
        const VERSION = '1.1.0';
        console.log(
            `%c sf-relation %c Version v${VERSION} %c`,
            'background:#35495e ; padding: 1px; border-radius: 3px 0 0 3px;  color: #fff',
            'background:#41b883 ; padding: 1px; border-radius: 0 3px 3px 0;  color: #fff',
            'backgrount:transparent'
        );

        // 
        let canvasBackgroundColor = ref('transparent');
        let isNeedFixedTools = ref(false);
        let isNeedFixedTools4Bottom = ref(false);
        // 
        let seeksNodeIdIndex = ref(1);
        // 
        // const el = {
        //     offsetWidth: 500,
        //     offsetHeight: 500,
        //     offsetTop: 0,
        //     offsetLeft: 0
        // };
        // let search_text = '';
        // let instanceId = ref('');
        // let viewOffset = reactive({
        //     windowHeight: 500,
        //     positionTop: 100,
        //     left: 0,
        //     top: 0
        // });

        // 
        let seeksRGStore = reactive({});
        let graphSettingWrap = reactive({
            graphSetting: {}
        });
        let graphData = reactive({
            nodes: [],
            lines: [],
            rootNode: null,
            nodes_map: {},
            lines_map: {}
        });
        let nodeViewList = reactive([]);
        let lineViewList = reactive([]);
        let allLineColors = reactive([]);

        let viewSizeIsInited = ref(false);
        let isShowZoomCenter = ref(false);
        let debugPanelPosition = ref(true);
        let zoomCenter_of_newSize = reactive({
            x: 0,
            y: 0
        });
        let currentZoomSet = reactive({});
        let newZoomSet = reactive({});
        let alive = ref(true);

        // ref dom
        const rgCanvas = ref(null);
        const seeksRelationGraph = ref(null);
        const seeksRGCanvas = ref(null);

        // 
        seeksRGStore = SeeksRGStore.createNewStore(props.options || {});
        graphSettingWrap.graphSetting = seeksRGStore.graphSetting;
        graphSettingWrap.graphSetting.instanceId = 'SRG' + parseInt(Math.random() * 100000);

        // 
        const canvasSizeAndPosition = computed(() => {
            return {
                width: graphSettingWrap.graphSetting.canvasSize.width + 'px',
                height: graphSettingWrap.graphSetting.canvasSize.height + 'px',
                'margin-left': graphSettingWrap.graphSetting.canvasOffset.x + 'px',
                'margin-top': graphSettingWrap.graphSetting.canvasOffset.y + 'px',
                'background-color': canvasBackgroundColor,
                transform: 'scale(' + graphSettingWrap.graphSetting.canvasZoom / 100 + ',' + graphSettingWrap.graphSetting.canvasZoom / 100 + ')'
            };
        });

        // 
        watch(
            () => graphSettingWrap.graphSetting.fullscreen,
            (newV, oldV) => {
                if (oldV === true || oldV === false) {
                    screenfull.toggle(seeksRelationGraph.value);
                }
            }
        );

        onMounted(() => {
            init();
            window.addEventListener(
                'scroll',
                function () {
                    syncToolsPosition();
                }.bind(this)
            );
        });

        onBeforeUnmount(() => {
            alive.value = false;
            const elx = seeksRelationGraph.value;
            elx.remove();
        });

        /**
         * 
         */
        function init() {
            rgCanvas.value.style.setProperty('--stroke', "url('#" + graphSettingWrap.graphSetting.instanceId + "-lineStyle')");
            rgCanvas.value.style.setProperty('--markerEnd', "url('#" + graphSettingWrap.graphSetting.instanceId + "-arrow-default')");
            rgCanvas.value.style.setProperty('--markerEndChecked', "url('#" + graphSettingWrap.graphSetting.instanceId + "-arrow-checked')");
            cycleTask();
            resetViewSize();
            refreshNVAnalysisInfo();
            syncToolsPosition();
        }

        function cycleTask() {
            if (alive.value) {
                var _box = seeksRelationGraph.value.getBoundingClientRect();
                if (_box.width !== graphSettingWrap.graphSetting.viewSize.width || _box.height !== graphSettingWrap.graphSetting.viewSize.height) {
                    if (window.SeeksGraphDebug) {
                        console.log('[sf-relation]viewSizeChanged:', [graphSettingWrap.graphSetting.viewSize.width, graphSettingWrap.graphSetting.viewSize.height], [_box.width, _box.height]);
                    }
                    graphSettingWrap.graphSetting.viewSize.width = _box.width;
                    graphSettingWrap.graphSetting.viewSize.height = _box.height;
                    resetViewSize();
                    refreshNVAnalysisInfo();
                    wow();
                    if (viewSizeIsInited.value && graphSettingWrap.graphSetting.moveToCenterWhenResize) {
                        if (window.SeeksGraphDebug) {
                            console.log('[sf-relation]moveToCenter:', [graphSettingWrap.graphSetting.viewSize.width, graphSettingWrap.graphSetting.viewSize.height], [_box.width, _box.height]);
                        }
                        nextTick(() => {
                            // focusRootNode()
                            var _min_x = 9999999;
                            var _min_y = 9999999;
                            var _max_x = 0;
                            var _max_y = 0;
                            nodeViewList.forEach((thisNode) => {
                                if (thisNode.x < _min_x) {
                                    _min_x = thisNode.x;
                                }
                                if (thisNode.x > _max_x) {
                                    _max_x = thisNode.x + thisNode.el.offsetWidth;
                                }
                                if (thisNode.y < _min_y) {
                                    _min_y = thisNode.y;
                                }
                                if (thisNode.y > _max_y) {
                                    _max_y = thisNode.y + thisNode.el.offsetHeight;
                                }
                            });
                            var _stuff_width = _max_x - _min_x + 50;
                            var _stuff_height = _max_y - _min_y + 50;
                            var _final_x = (graphSettingWrap.graphSetting.viewSize.width - _stuff_width) / 2 - _min_x;
                            var _final_y = (graphSettingWrap.graphSetting.viewSize.height - _stuff_height) / 2 - _min_y;
                            // if (window.SeeksGraphDebug) console.log('_min_x, _min_y, _stuff_width, _stuff_height:', _min_x, _min_y, _stuff_width, _stuff_height)
                            // var _final_x = -_min_x
                            // var _final_y = -_min_y
                            animateGoto(_final_x, _final_y, 500, () => {
                                // graphSettingWrap.graphSetting.checkedNodeId = thisNode.id
                                refreshNVAnalysisInfo();
                            });
                        });
                    }
                }
                setTimeout(() => {
                    cycleTask();
                }, 1000);
            }
        }

        function refreshNVAnalysisInfo() {
            if (!seeksRelationGraph.value) {
                console.error('cannot get view size !');
                return;
            }
            // console.log('reanalysis NV info...')
            var result = {
                NMCanvasCenter: { x: 0, y: 0 }
            };
            var _view_info = seeksRelationGraph.value.getBoundingClientRect();
            graphSettingWrap.graphSetting.viewNVInfo.x = _view_info.left;
            graphSettingWrap.graphSetting.viewNVInfo.y = _view_info.top;
            graphSettingWrap.graphSetting.viewNVInfo.width = _view_info.width;
            graphSettingWrap.graphSetting.viewNVInfo.height = _view_info.height;
            var _NM_canvas_width = graphSettingWrap.graphSetting.canvasSize.width * (graphSettingWrap.graphSetting.canvasZoom / 100);
            var _NM_canvas_height = graphSettingWrap.graphSetting.canvasSize.height * (graphSettingWrap.graphSetting.canvasZoom / 100);
            result.NMCanvasCenter.x = graphSettingWrap.graphSetting.canvasOffset.x + graphSettingWrap.graphSetting.canvasSize.width / 2;
            result.NMCanvasCenter.y = graphSettingWrap.graphSetting.canvasOffset.y + graphSettingWrap.graphSetting.canvasSize.height / 2;
            graphSettingWrap.graphSetting.canvasNVInfo.x = result.NMCanvasCenter.x - _NM_canvas_width / 2;
            graphSettingWrap.graphSetting.canvasNVInfo.y = result.NMCanvasCenter.y - _NM_canvas_height / 2;
            graphSettingWrap.graphSetting.canvasNVInfo.width = _NM_canvas_width;
            graphSettingWrap.graphSetting.canvasNVInfo.height = _NM_canvas_height;
            graphSettingWrap.graphSetting.viewELSize.width = _view_info.width;
            graphSettingWrap.graphSetting.viewELSize.height = _view_info.height;
            graphSettingWrap.graphSetting.viewELSize.left = _view_info.left;
            graphSettingWrap.graphSetting.viewELSize.top = _view_info.top;
        }

        /**
         * 
         */
        function resetViewSize() {
            graphSettingWrap.graphSetting.viewSize.width = seeksRelationGraph.value.getBoundingClientRect().width;
            graphSettingWrap.graphSetting.viewSize.height = seeksRelationGraph.value.getBoundingClientRect().height;
            graphSettingWrap.graphSetting.canvasZoom = 100;
            seeksRGStore.resetViewSize();
            refreshNVAnalysisInfo();
        }

        /**
         * 
         */
        function syncToolsPosition() {
            if (!seeksRelationGraph.value) {
                return;
            }
            const windowHeight = getWindowHeight();
            var _box_info = seeksRelationGraph.value.getBoundingClientRect();
            var __top = _box_info.top;
            if (isNeedFixedTools.value === false) {
                if (__top + seeksRelationGraph.value.offsetHeight < 0) {
                    isNeedFixedTools.value = false;
                } else {
                    if (__top < 0) {
                        isNeedFixedTools.value = true;
                    }
                }
            } else {
                if (__top > 0) {
                    isNeedFixedTools.value = false;
                }
                if (__top + seeksRelationGraph.value.offsetHeight < 0) {
                    isNeedFixedTools.value = false;
                }
            }
            if (__top + seeksRelationGraph.value.offsetHeight > windowHeight) {
                isNeedFixedTools4Bottom.value = true;
            } else {
                isNeedFixedTools4Bottom.value = false;
            }
        }

        /**
         *  - 
         */
        function wow() {
            graphSettingWrap.graphSetting.canvasOffset.x = graphSettingWrap.graphSetting.canvasOffset.x + 1;
            graphSettingWrap.graphSetting.canvasOffset.y = graphSettingWrap.graphSetting.canvasOffset.y + 1;
            graphSettingWrap.graphSetting.canvasOffset.x = graphSettingWrap.graphSetting.canvasOffset.x - 1;
            graphSettingWrap.graphSetting.canvasOffset.y = graphSettingWrap.graphSetting.canvasOffset.y - 1;
            document.body.removeEventListener('mousemove', wow);
        }

        //  - node - click
        function onRGNodeClick(nodeData, e) {
            if (props.onNodeClick) {
                props.onNodeClick(nodeData, e);
            }
            // for (let i = 0; i < lineViewList.length; i++) {
            //   var thisLine = lineViewList[i]
            //   if (thisLine.fromNode.id === nodeData.id || thisLine.toNode.id === nodeData.id) {
            //     thisLine.flash = thisLine.flash + 1
            //   }
            // }
        }

        //  - line - click
        function onRGLineClick(lineData, e) {
            if (props.onLineClick) {
                props.onLineClick(lineData, e);
            }
            // for (let i = 0; i < lineViewList.length; i++) {
            //   var thisLine = lineViewList[i]
            //   if (thisLine.fromNode.id === nodeData.id || thisLine.toNode.id === nodeData.id) {
            //     thisLine.flash = thisLine.flash + 1
            //   }
            // }
        }

        // API - 
        function setOptions(options, callback) {
            seeksRGStore = SeeksRGStore.createNewStore(options);
            graphSettingWrap.graphSetting = seeksRGStore.graphSetting;
            graphSettingWrap.graphSetting.instanceId = 'SRG' + parseInt(Math.random() * 100000);
            init();
            if (graphSettingWrap.graphSetting.layouts && graphSettingWrap.graphSetting.layouts.length > 0) {
                var _defaultLayoutSetting = graphSettingWrap.graphSetting.layouts[0];
                if (window.SeeksGraphDebug) {
                    console.log('', graphSettingWrap.graphSetting.layoutName);
                }
                if (_defaultLayoutSetting.layouter) {
                    graphSettingWrap.graphSetting.layouter = _defaultLayoutSetting.layouter;
                } else {
                    graphSettingWrap.graphSetting.layouter = SeeksRGLayouters.createLayout(_defaultLayoutSetting, graphSettingWrap.graphSetting);
                }
            } else {
                console.log('layouts');
            }
            doLayout();
            callback(this);
        }

        /**
         * 
         */
        function doLayout() {
            if (graphSettingWrap.graphSetting.layouter && graphData.rootNode) {
                console.log('[sf-relation]layout', graphData.nodes);
                graphSettingWrap.graphSetting.layouter.placeNodes(graphData.nodes, graphData.rootNode);
            }
            document.body.addEventListener('mousemove', wow);
            // document.body.removeEventListener('mousemove', graphOnClick)
        }

        /**
         * debug
         */
        function moveDebugPanel() {
            debugPanelPosition.value = !debugPanelPosition.value;
        }

        /**
         *  -  -  
         */
        function mouseListener(e) {
            // if (e.target !== $refs.seeksRGCanvas) {
            //   return
            // }
            // e.stopPropagation()
            // console.log('mouseListener')
            if (graphSettingWrap.graphSetting.disableZoom) {
                e.cancelBubble = false;
                return true;
            }
            try {
                e.cancelBubble = true;
                e.preventDefault();
                e.stopPropagation();
            } catch (e) {
                // xxx
            }
            var userZoomCenter = {
                x: e.clientX,
                y: e.clientY
            };
            // console.log('---- center:', userZoomCenter.x, userZoomCenter.y)
            var _isMac = /macintosh|mac os x/i.test(navigator.userAgent);
            var _deltaY = e.deltaY;
            if (_deltaY === undefined) {
                _deltaY = e.wheelDelta;
            }
            // console.log('mouseListenerEmpty:', _isMac, e.deltaY, e.wheelDelta, e.which, e.detail)
            var _zoomDirection = _isMac ? 1 : -1;
            if (_deltaY > 0) {
                zoom(5 * _zoomDirection, userZoomCenter);
            } else {
                zoom(-5 * _zoomDirection, userZoomCenter);
            }
        }

        /**
         *  -  - zoom
         */
        function zoom(buff, userZoomCenter) {
            if (graphSettingWrap.graphSetting.canvasZoom + buff < 10) {
                return;
            }
            var __new_zoom_value = graphSettingWrap.graphSetting.canvasZoom + buff;
            var zoomCenter = showZoomCenter(userZoomCenter, buff);
            // console.log('zoomCenter:', zoomCenter.x, zoomCenter.y)
            // if (userZoomCenter) return
            // graphSettingWrap.graphSetting.canvasOffset.zoom_buff_x = zoomCenter.buff_x
            // graphSettingWrap.graphSetting.canvasOffset.zoom_buff_y = zoomCenter.buff_y
            // var _buff_x = canvasCenter.x - zoomCenter.x
            // var _buff_y = canvasCenter.y - zoomCenter.y
            // console.log('offset buff:', _buff_x.toFixed(0), _buff_y.toFixed(0))
            // graphSettingWrap.graphSetting.canvasOffset.zoom_buff_x = _buff_x * ((graphSettingWrap.graphSetting.canvasZoom - 100) / 100)
            // graphSettingWrap.graphSetting.canvasOffset.zoom_buff_y = _buff_y * ((graphSettingWrap.graphSetting.canvasZoom - 100) / 100)
            graphSettingWrap.graphSetting.canvasOffset.x += zoomCenter.buff_x;
            graphSettingWrap.graphSetting.canvasOffset.y += zoomCenter.buff_y;
            graphSettingWrap.graphSetting.canvasZoom = __new_zoom_value;
            refreshNVAnalysisInfo();
        }

        /**
         *  - zoom - center
         */
        function showZoomCenter(userZoomCenter, zoomBuff) {
            if (!seeksRelationGraph.value) {
                return;
            }
            var _current_zoom = graphSettingWrap.graphSetting.canvasZoom / 100;
            var _new_zoom = (graphSettingWrap.graphSetting.canvasZoom + zoomBuff) / 100;
            currentZoomSet = analysisByZoom(_current_zoom, userZoomCenter);
            newZoomSet = analysisByZoom(_new_zoom, userZoomCenter);
            // console.log('currentZoomSet:', currentZoomSet)
            // console.log('currentZoomSet:', newZoomSet)
            const a = _new_zoom / _current_zoom;
            const b = 0;
            const c = 0;
            const d = _new_zoom / _current_zoom;
            var e = 0;
            var f = 0;
            zoomCenter_of_newSize.x = a * currentZoomSet.NMViewBuff.x + c * currentZoomSet.NMViewBuff.y + e;
            zoomCenter_of_newSize.y = b * currentZoomSet.NMViewBuff.x + d * currentZoomSet.NMViewBuff.y + f;
            var buff_x = currentZoomSet.NMViewBuff.x - zoomCenter_of_newSize.x;
            var buff_y = currentZoomSet.NMViewBuff.y - zoomCenter_of_newSize.y;
            zoomCenter_of_newSize.x += currentZoomSet.NMCanvasCenter.x;
            zoomCenter_of_newSize.y += currentZoomSet.NMCanvasCenter.y;
            // e = currentZoomSet.NMViewBuff.x
            // f = currentZoomSet.NMViewBuff.y
            // new start
            var old_x = currentZoomSet.NMCanvasStart.x - currentZoomSet.NMCanvasCenter.x;
            var old_y = currentZoomSet.NMCanvasStart.y - currentZoomSet.NMCanvasCenter.y;
            var new_x = a * old_x + c * old_y + e;
            var new_y = b * old_x + d * old_y + f;
            newZoomSet.NMCanvasStart.x = buff_x + currentZoomSet.NMCanvasCenter.x + new_x;
            newZoomSet.NMCanvasStart.y = buff_x + currentZoomSet.NMCanvasCenter.y + new_y;
            // new end
            old_x = currentZoomSet.NMCanvasEnd.x - currentZoomSet.NMCanvasCenter.x;
            old_y = currentZoomSet.NMCanvasEnd.y - currentZoomSet.NMCanvasCenter.y;
            new_x = a * old_x + c * old_y + e;
            new_y = b * old_x + d * old_y + f;
            newZoomSet.NMCanvasEnd.x = buff_x + currentZoomSet.NMCanvasCenter.x + new_x;
            newZoomSet.NMCanvasEnd.y = buff_x + currentZoomSet.NMCanvasCenter.y + new_y;
            currentZoomSet.NMCanvasOffsetBuff.x = buff_x;
            currentZoomSet.NMCanvasOffsetBuff.y = buff_y;
            // isShowZoomCenter = true
            return {
                buff_x,
                buff_y
            };
        }

        /**
         * zoom
         */
        function analysisByZoom(zoom, userZoomCenter) {
            var result = {
                NMViewPosition: { x: 0, y: 0 },
                NMViewCenter: { x: 0, y: 0 },
                NMCanvasCenter: { x: 0, y: 0 },
                NMCanvasStart: { x: 0, y: 0 },
                NMCanvasEnd: { x: 0, y: 0 },
                NMZoomCenter: { x: 0, y: 0 },
                NMViewBuff: { x: 0, y: 0 },
                NMCanvasOffsetBuff: { x: 0, y: 0 },
                NMCanvasSize: { width: 0, height: 0 }
            };
            const windowWidth = getWindowWidth();
            const windowHeight = getWindowHeight();
            var _view_info = seeksRelationGraph.value.getBoundingClientRect();
            result.NMViewPosition.x = _view_info.left;
            result.NMViewPosition.y = _view_info.top;
            if (_view_info.width + result.NMViewPosition.x > windowWidth) {
                result.NMViewCenter.x = (windowWidth - _view_info.left) / 2;
            } else {
                result.NMViewCenter.x = _view_info.width / 2;
            }
            if (_view_info.height + result.NMViewPosition.y > windowHeight) {
                result.NMViewCenter.y = (windowHeight - _view_info.top) / 2;
            } else {
                result.NMViewCenter.y = _view_info.height / 2;
            }
            var _NM_canvas_width = graphSettingWrap.graphSetting.canvasSize.width * zoom;
            var _NM_canvas_height = graphSettingWrap.graphSetting.canvasSize.height * zoom;
            result.NMCanvasCenter.x = graphSettingWrap.graphSetting.canvasOffset.x + graphSettingWrap.graphSetting.canvasSize.width / 2; // + (graphSettingWrap.graphSetting.canvasOffset.zoom_buff_x * _NM_canvas_width)
            result.NMCanvasCenter.y = graphSettingWrap.graphSetting.canvasOffset.y + graphSettingWrap.graphSetting.canvasSize.height / 2; // + (graphSettingWrap.graphSetting.canvasOffset.zoom_buff_y * _NM_canvas_height)
            result.NMCanvasStart.x = result.NMCanvasCenter.x - _NM_canvas_width / 2;
            result.NMCanvasStart.y = result.NMCanvasCenter.y - _NM_canvas_height / 2;
            result.NMCanvasEnd.x = result.NMCanvasCenter.x + _NM_canvas_width / 2;
            result.NMCanvasEnd.y = result.NMCanvasCenter.y + _NM_canvas_height / 2;
            result.NMZoomCenter.x = result.NMViewCenter.x;
            result.NMZoomCenter.y = result.NMViewCenter.y;
            if (userZoomCenter) {
                result.NMZoomCenter.x = userZoomCenter.x - result.NMViewPosition.x;
                result.NMZoomCenter.y = userZoomCenter.y - result.NMViewPosition.y;
            }
            var _NM_buff_x = result.NMViewCenter.x - result.NMCanvasCenter.x;
            var _NM_buff_y = result.NMViewCenter.y - result.NMCanvasCenter.y;
            if (userZoomCenter) {
                _NM_buff_x = result.NMZoomCenter.x - result.NMCanvasCenter.x;
                _NM_buff_y = result.NMZoomCenter.y - result.NMCanvasCenter.y;
            }
            result.NMViewBuff.x = _NM_buff_x;
            result.NMViewBuff.y = _NM_buff_y;
            result.NMCanvasSize.width = _NM_canvas_width;
            result.NMCanvasSize.height = _NM_canvas_height;
            return result;
        }

