        /**
         * API - 
         */
        function getPositionOfCanvas(e) {
            var userZoomCenter = {
                x: e.offsetX,
                y: e.offsetY
            };
            if (window.SeeksGraphDebug) {
                console.log('[F]', userZoomCenter.x, userZoomCenter.y);
            }
            var currentNode = e.target.parentNode;
            for (var i = 0; i < 8; i++) {
                if (i > 6) {
                    if (window.SeeksGraphDebug) {
                        console.log('getPositionOfCanvas error', e);
                    }
                }
                if (currentNode.classList.contains('rel-map-canvas')) {
                    if (window.SeeksGraphDebug) {
                        console.log('[S]', currentNode.tagName + '.' + currentNode.className);
                    }
                    break;
                } else {
                    userZoomCenter.x += currentNode.offsetLeft || 0;
                    userZoomCenter.y += currentNode.offsetTop || 0;
                    if (window.SeeksGraphDebug) {
                        console.log('[' + i + ']', currentNode.tagName + '.' + currentNode.className, ':', currentNode.offsetLeft, currentNode.offsetTop);
                    }
                    currentNode = currentNode.parentNode;
                }
            }
            if (window.SeeksGraphDebug) {
                console.log('[F]', userZoomCenter.x, userZoomCenter.y);
            }
            return userZoomCenter;
        }

        /**
         * API - 
         */
        function getNodePositionTop(node) {
            if (!node.offsetTop) {
                return 0;
            }
            return node.offsetTop + (node.parentNode ? getNodePositionTop(node.parentNode) : 0);
        }

        /**
         * API - 
         */
        function getNodePositionLeft(node) {
            if (!node.offsetLeft) {
                return 0;
            }
            return node.offsetLeft + (node.parentNode ? getNodePositionLeft(node.parentNode) : 0);
        }

        /**
         * 
         */
        function loadNodes(_nodes) {
            _nodes.forEach((thisNodeJson) => {
                let thisNode = SeeksRGUtils.json2Node(thisNodeJson);
                let __isNew = false;
                if (graphData.nodes_map[thisNode.id]) {
                    thisNode = graphData.nodes_map[thisNode.id];
                } else {
                    __isNew = true;
                }
                if (__isNew) {
                    graphData.nodes_map[thisNode.id] = thisNode;
                    graphData.nodes.push(thisNode);
                    thisNode.seeks_id = seeksNodeIdIndex.value++;
                    thisNode.appended = false;
                }
            });
        }

        /**
         * 
         */
        function loadLinks(_links) {
            _links.forEach((thisLinkJson) => {
                let __isNew = false;
                var __from;
                var __to;
                if (typeof thisLinkJson.from === 'object') {
                    __from = thisLinkJson.from;
                } else {
                    __from = graphData.nodes_map[thisLinkJson.from];
                }
                if (typeof thisLinkJson.to === 'object') {
                    __to = thisLinkJson.to;
                } else {
                    __to = graphData.nodes_map[thisLinkJson.to];
                }
                if (!__from) {
                    console.error('from:', thisLinkJson);
                    return;
                }
                if (!__to) {
                    console.error('to:', thisLinkJson);
                    return;
                }
                // console.log('[add link]', __from.text, __to.text, __from.seeks_id, __to.seeks_id, thisLink)
                const lineId1 = __from.seeks_id + '-' + __to.seeks_id;
                const lineId2 = __to.seeks_id + '-' + __from.seeks_id;
                var thisLink = SeeksRGUtils.json2Link(thisLinkJson);
                var thisLine;
                var thisLinkIsReserve = false;
                if (graphData.lines_map[lineId1]) {
                    thisLine = graphData.lines_map[lineId1];
                } else {
                    if (graphData.lines_map[lineId2]) {
                        thisLine = graphData.lines_map[lineId2];
                        thisLinkIsReserve = true;
                    } else {
                        __isNew = true;
                        thisLine = {
                            seeks_id: lineId1,
                            fromNode: __from,
                            toNode: __to,
                            appended: false,
                            relations: []
                        };
                    }
                }
                // console.log('new Line Color:', thisLine.color, thisLine.arrow)
                var _arrow = thisLink.arrow;
                if (thisLink.isHideArrow) {
                    // do nothing
                } else {
                    _arrow = getLineArrow(thisLink.color);
                }
                if (!__from.targetNodes) __from.targetNodes = [];
                if (!__to.targetNodes) __to.targetNodes = [];
                if (__from.targetNodes.indexOf(__to) === -1) {
                    __from.targetNodes.push(__to);
                }
                if (__to.targetNodes.indexOf(__from) === -1) {
                    __to.targetNodes.push(__from);
                }
                if (__from.targetTo.indexOf(__to) === -1) {
                    __from.targetTo.push(__to);
                }
                if (__to.targetFrom.indexOf(__from) === -1) {
                    __to.targetFrom.push(__from);
                }
                var isDuplicate = false;
                for (var i = 0; i < thisLine.relations.length; i++) {
                    if (thisLine.relations[i].id === thisLink.id) {
                        isDuplicate = true;
                        break;
                    }
                }
                if (isDuplicate === false) {
                    if (!thisLink.id) thisLink.id = thisLine.seeks_id + '-' + thisLine.relations.length;
                    thisLink.isReverse = thisLinkIsReserve;
                    thisLink.arrow = _arrow;
                    thisLink.textPositon = { x: 0, y: 0 };
                    thisLine.relations.push(thisLink);
                }
                // console.log('addLine:', thisLine)
                if (__isNew) {
                    graphData.lines.push(thisLine);
                    graphData.lines_map[lineId1] = thisLine;
                    thisLine.appended = false;
                }
            });
        }

        /**
         * 
         */
        function getLineArrow(_color) {
            if (_color) {
                var thisColorId = SeeksRGUtils.getColorId(_color);
                if (
                    allLineColors
                        .map((thisColorObj) => {
                            return thisColorObj.id;
                        })
                        .indexOf(thisColorId) === -1
                ) {
                    allLineColors.push({ id: thisColorId, color: _color });
                }
                return graphSettingWrap.graphSetting.instanceId + '-arrow-' + thisColorId;
            } else {
                return graphSettingWrap.graphSetting.instanceId + '-arrow-default';
            }
        }

        /**
         *  
         */
        function flatNodeData(orign_nodes, parentNode, nodes_collect, links_collect) {
            orign_nodes.forEach((thisOrignNode) => {
                if (!thisOrignNode.flated) {
                    thisOrignNode.flated = true;
                    nodes_collect.push(thisOrignNode);
                    if (parentNode) {
                        links_collect.push({
                            from: parentNode.id,
                            to: thisOrignNode.id
                        });
                    }
                    var _childs = thisOrignNode.childs || thisOrignNode.children;
                    if (_childs && _childs.length > 0) {
                        flatNodeData(_childs, thisOrignNode, nodes_collect, links_collect);
                    }
                }
            });
        }

        /**
         * API - 
         */
        function loadGraphJsonData(jsonData) {
            // 
            // if (!jsonData.links) {
            //     jsonData.links = jsonData.lines;
            // }
            // if (!jsonData.links) {
            //     jsonData.links = jsonData.relations;
            // }
            var _orign_nodes = jsonData.nodes;
            var _nodes = [];
            var _links = [];
            flatNodeData(_orign_nodes, null, _nodes, _links);
            jsonData.links = _links.concat(jsonData.links);
            loadNodes(_nodes);
            loadLinks(jsonData.links);
        }

        /**
         * API - 
         */
        function setJsonData(jsonData, callback) {
            viewSizeIsInited.value = true;
            // nodeViewList = [];
            nodeViewList.length = 0;
            // lineViewList = [];
            lineViewList.length = 0;
            graphData.nodes = [];
            graphData.lines = [];
            graphData.nodes_map = {};
            graphData.lines_map = {};
            graphData.rootNode = null;
            console.log('[sf-relation]jsonData:', jsonData);
            resetViewSize();
            if (graphSettingWrap.graphSetting.layouts && graphSettingWrap.graphSetting.layouts.length > 0) {
                var _defaultLayoutSetting = graphSettingWrap.graphSetting.layouts[0];
                if (window.SeeksGraphDebug) {
                    console.log('[sf-relation]layoutType:', graphSettingWrap.graphSetting.layoutName);
                }
                if (_defaultLayoutSetting.layouter) {
                    graphSettingWrap.graphSetting.layouter = _defaultLayoutSetting.layouter;
                } else {
                    graphSettingWrap.graphSetting.layouter = SeeksRGLayouters.createLayout(_defaultLayoutSetting, graphSettingWrap.graphSetting);
                }
            } else {
                console.log('layouts');
            }
            var __root_id = jsonData['rootId'];
            loadGraphJsonData(jsonData);
            // console.log('graphData:', graphData)
            if (__root_id) {
                graphData.rootNode = graphData.nodes_map[__root_id];
            }
            if (!graphData.rootNode && graphData.nodes.length > 0) {
                graphData.rootNode = graphData.nodes[0];
            }
            applyNewDataToCanvas();
            resetViewSize();
            doLayout();
            if (callback) {
                callback(this);
            }
        }

        /**
         * 
         */
        function applyNewDataToCanvas() {
            graphData.nodes.forEach((thisNode) => {
                if (thisNode.appended === false) {
                    thisNode.appended = true;
                    nodeViewList.push(thisNode);
                }
            });
            graphData.lines.forEach((thisLine) => {
                if (thisLine.appended === false) {
                    thisLine.appended = true;
                    lineViewList.push(thisLine);
                }
            });
            if (graphData.rootNode) {
                if (graphSettingWrap.graphSetting.defaultFocusRootNode) {
                    graphSettingWrap.graphSetting.checkedNodeId = graphData.rootNode.id;
                }
            } else {
                throw Error('[rootId]!');
            }
        }
        /**
         * 
         */
        function appendJsonData(jsonData, isRelayout, callback) {
            if (arguments.length === 2 && typeof isRelayout === 'function') {
                callback = isRelayout;
                isRelayout = true;
            }
            console.log('appendData:', jsonData);
            loadGraphJsonData(jsonData);
            applyNewDataToCanvas();
            // resetViewSize()
            if (isRelayout) {
                doLayout();
            }
            if (callback) {
                callback(this);
            }
        }

