        /**
         * API - ID
         */
        function getNodeById(nodeId) {
            for (let i = 0; i < nodeViewList.length; i++) {
                if (nodeViewList[i].id === nodeId) {
                    return nodeViewList[i];
                }
            }
        }

        /**
         * API - 
         */
        function removeNodeById(nodeId) {
            let __removed_lines = 0;
            for (let i = 0; i < lineViewList.length; i++) {
                var thisLine = lineViewList[i];
                if (thisLine.fromNode.id === nodeId || thisLine.toNode.id === nodeId) {
                    // console.log(lineViewList[i])
                    thisLine.hidden = true;
                    lineViewList.splice(i, 1);
                    i--;
                    __removed_lines++;
                    // console.log(lineViewList[i])
                }
            }
            console.log('', nodeId, __removed_lines);
            let __removed_nodes = 0;
            for (let i = 0; i < nodeViewList.length; i++) {
                if (nodeViewList[i].id === nodeId) {
                    const thisNode = nodeViewList[i];
                    thisNode.targetNodes.forEach((thisTNode) => {
                        const t_i = thisTNode.targetNodes.indexOf(thisNode);
                        if (t_i !== -1) {
                            thisTNode.targetNodes.splice(t_i, 1);
                        }
                    });
                    // thisNode.isShow = false
                    nodeViewList.splice(i, 1);
                    delete graphData.nodes_map[thisNode.id];
                    const d_i = graphData.nodes.findIndex((k) => k.id === thisNode.id);
                    graphData.nodes.splice(d_i, 1);
                    __removed_nodes++;
                    break;
                }
            }
            console.log('', nodeId, __removed_nodes);
        }

        /**
         * data  blob
         */
        function dataURLToBlob(dataurl) {
            // ie 
            var arr = dataurl.split(',');
            var mime = arr[0].match(/:(.*?);/)[1];
            var bstr = atob(arr[1]);
            var n = bstr.length;
            var u8arr = new Uint8Array(n);
            while (n--) {
                u8arr[n] = bstr.charCodeAt(n);
            }
            return new Blob([u8arr], { type: mime });
        }

        /**
         * API - 
         */
        function downloadAsImage(format) {
            if (props.beforeDownloadImage) {
                props.beforeDownloadImage();
            }
            // console.log('window.navigator.msSaveOrOpenBlob:', window.navigator.msSaveOrOpenBlob)
            // console.log('window.navigator.msSaveBlob', window.navigator.msSaveBlob)
            // if (!!window.ActiveXObject || 'ActiveXObject' in window) {
            //   $message('IE!')
            //   return
            // }
            if (nodeViewList.length === 0) {
                throw Error('');
            }
            if (!format) {
                format = 'png';
            }
            graphSettingWrap.graphSetting.checkedNodeId = '';
            graphSettingWrap.graphSetting.canvasZoom = 100;
            const exportDom = seeksRGCanvas.value;
            var orign_width = exportDom.clientWidth; // dom 
            var orign_height = exportDom.clientHeight; // dom 
            var _min_x = 999999;
            var _min_y = 999999;
            var _max_x = 0;
            var _max_y = 0;
            var _padding = 100;
            nodeViewList.forEach((thisNode) => {
                if (thisNode.x < _min_x) {
                    _min_x = thisNode.x;
                }
                if (thisNode.x > _max_x) {
                    _max_x = thisNode.x + thisNode.el.offsetWidth;
                }
                if (thisNode.y < _min_y) {
                    _min_y = thisNode.y;
                }
                if (thisNode.y > _max_y) {
                    _max_y = thisNode.y + thisNode.el.offsetHeight;
                }
            });
            nodeViewList.forEach((thisNode) => {
                thisNode.x = thisNode.x - _min_x + _padding;
                thisNode.y = thisNode.y - _min_y + _padding;
            });
            var _origin_offset_x = graphSettingWrap.graphSetting.canvasOffset.x + _min_x - _padding;
            var _origin_offset_y = graphSettingWrap.graphSetting.canvasOffset.y + _min_y - _padding;
            graphSettingWrap.graphSetting.canvasOffset.x = _padding * -1;
            graphSettingWrap.graphSetting.canvasOffset.y = _padding * -1;
            console.log('offset:', { _origin_offset_x, _origin_offset_y, _min_x, _min_y, _max_x, _max_y });

            var _image_width = _max_x - _min_x + 200 + _padding * 2;
            var _image_height = _max_y - _min_y + 100 + _padding * 2;
            var pixelRatio = window.devicePixelRatio; //  
            graphSettingWrap.graphSetting.canvasSize.width = _image_width * pixelRatio;
            graphSettingWrap.graphSetting.canvasSize.height = _image_height * pixelRatio;
            canvasBackgroundColor.value = '#ffffff';

            var relationGraphPosition = {
                left: seeksRelationGraph.value.offsetLeft - exportDom.getBoundingClientRect().left,
                top: seeksRelationGraph.value.offsetTop - exportDom.getBoundingClientRect().top,
                canvas_offsetLeft: exportDom.offsetLeft,
                canvas_offsetTop: exportDom.offsetTop,
                canvas_left: exportDom.getBoundingClientRect().left,
                canvas_top: exportDom.getBoundingClientRect().top
            };
            // exportDom.style.position ='absolute'
            // exportDom.style.left ='0px'
            // exportDom.style.top ='0px'
            // exportDom.style.zIndex ='999'
            window.scrollTo(0, 0);
            console.log('export image:', { relationGraphPosition, orign_width, orign_height, _image_width, _image_height, _min_x, _min_y, _max_x, _max_y, devicePixelRatio: window.devicePixelRatio });
            nextTick(() => {
                var fileName = 'SeeksRelationGraph-' + (Math.random() * 100000).toFixed(0);
                var canvas = document.createElement('canvas'); // canvas
                canvas.width = _image_width * pixelRatio; // canvas  * 
                canvas.height = _image_height * pixelRatio; // canvas *
                canvas.style.width = _image_width * pixelRatio + 'px';
                canvas.style.height = _image_height * pixelRatio + 'px';
                canvas.getContext('2d').scale(1, 1); // context,scale
                const opts = {
                    backgroundColor: null,
                    scale: pixelRatio, // scale 
                    canvas: canvas, //  canvas
                    logging: true, // html2canvas
                    // windowWidth: _image_width,
                    // windowHeight: _image_height,
                    width: _image_width, // dom 
                    height: _image_height,
                    // x: relationGraphPosition.left,
                    // y: relationGraphPosition.top,
                    useCORS: true // 
                };
                console.log('html2canvas:', opts);
                html2canvas(exportDom, opts).then((canvas) => {
                    const dom = document.body.appendChild(canvas);
                    // console.log('canvas:', fileName, dom)
                    dom.style.display = 'none';
                    const blob = dataURLToBlob(dom.toDataURL('image/' + format));
                    document.body.removeChild(dom);
                    const a = document.createElement('a');
                    a.style.display = 'none';
                    try {
                        if (window.navigator.msSaveOrOpenBlob) {
                            window.navigator.msSaveOrOpenBlob(blob, fileName + '.' + format);
                            // console.log('this is IE');
                            // var URL=window.URL;
                            // var BlobBuilder = window.MSBlobBuilder;
                            // navigator.saveBlob=navigator.msSaveBlob;
                            // var imgBlob = canvas.msToBlob();
                            // if (BlobBuilder && navigator.saveBlob) {
                            //   var showSave =  function (data, name, mimetype) {
                            //     var builder = new BlobBuilder();
                            //     builder.append(data);
                            //     var blob = builder.getBlob(mimetype||"application/octet-stream");
                            //     if (!name)
                            //       name = "Download.bin";
                            //     navigator.saveBlob(blob, name);
                            //   };
                            //   showSave(imgBlob, 'barchart.png',"image/png");
                            // }
                        } else {
                            a.setAttribute('href', URL.createObjectURL(blob));
                            a.setAttribute('download', fileName + '.' + format);
                            document.body.appendChild(a);
                            // console.log('click to download:', opts)
                            a.click();
                            console.log('click ok!');
                            URL.revokeObjectURL(blob);
                            console.log('revokeObjectURL ok!');
                            document.body.removeChild(a);
                            console.log('removeChild ok!');
                        }
                        graphSettingWrap.graphSetting.canvasSize.width = orign_width;
                        graphSettingWrap.graphSetting.canvasSize.height = orign_height;
                        graphSettingWrap.graphSetting.canvasOffset.x = _origin_offset_x;
                        graphSettingWrap.graphSetting.canvasOffset.y = _origin_offset_y;
                        canvasBackgroundColor = 'transparent';
                    } catch (e) {
                        console.log('[SEEKS Graph]Create and download image error:', e);
                    }
                });
            });
        }

        /**
         * API - 
         */
        function querySearchAsync(queryString, callback) {
            console.log('fetch-suggestions', queryString);
            queryString = queryString.trim();
            if (queryString === '') {
                return;
            }
            var rst = [];
            nodeViewList.forEach((thisNode) => {
                console.log('fetch:', thisNode.text);
                if (thisNode.text.indexOf(queryString) !== -1) {
                    rst.push(thisNode);
                }
            });
            console.log('fetched:', rst.length);
            callback(rst);
        }

        /**
         * API - 
         */
        function focusRootNode() {
            if (window.SeeksGraphDebug) {
                console.log('relation-graph:focusRootNode');
            }
            handleSelect(graphData.rootNode);
        }

        /**
         * API - 
         */
        function focusNodeById(nodeId) {
            graphData.nodes.forEach((thisNode) => {
                if (thisNode.id === nodeId) {
                    handleSelect(thisNode);
                }
            });
        }

        /**
         * 
         */
        function handleSelect(thisNode) {
            console.log('checked:', thisNode);
            scrollTo({
                top: seeksRelationGraph.value.offsetTop
            });
            animateToZoom(100, 300, () => {
                var _n_width = thisNode.width || 50;
                var _n_height = thisNode.height || 50;
                var _final_x = thisNode.x * -1 + graphSettingWrap.graphSetting.viewSize.width / 2 - _n_width / 2;
                var _final_y = thisNode.y * -1 + graphSettingWrap.graphSetting.viewSize.height / 2 - _n_height / 2;
                animateGoto(_final_x, _final_y, 500, () => {
                    graphSettingWrap.graphSetting.checkedNodeId = thisNode.id;
                    refreshNVAnalysisInfo();
                });
            });
        }

        /**
         *  - 
         */
        function animateGoto(x, y, time, callback) {
            var _distance_x = x - graphSettingWrap.graphSetting.canvasOffset.x;
            var _distance_y = y - graphSettingWrap.graphSetting.canvasOffset.y;
            var _allTime = time;
            var _allStepNum = 5;
            var _speed_x = parseInt(_distance_x / _allStepNum);
            var _speed_y = parseInt(_distance_y / _allStepNum);
            var _perDelay = _allTime / _allStepNum;
            animateStepAction(
                0,
                _perDelay,
                _allStepNum,
                () => {
                    graphSettingWrap.graphSetting.canvasOffset.x += _speed_x;
                    graphSettingWrap.graphSetting.canvasOffset.y += _speed_y;
                },
                () => {
                    // console.log('....')
                    callback();
                }
            );
        }

        /**
         *  -  - 
         */
        function animateStepAction(stepIndex, delay, allStepNum, stepCallback, finalCallback) {
            // console.log(Date.now() + '[' + stepIndex + ']')
            if (stepIndex < allStepNum) {
                stepCallback(stepIndex, allStepNum);
                setTimeout(() => {
                    animateStepAction(stepIndex + 1, delay, allStepNum, stepCallback, finalCallback);
                }, delay);
            } else {
                finalCallback();
            }
        }

        /**
         *  - 
         */
        function animateToZoom(finalZoom, time, callback) {
            var _zoom_distance = finalZoom - graphSettingWrap.graphSetting.canvasZoom;
            var _allTime = time;
            var _allStepNum = 5;
            var _speed = parseInt(_zoom_distance / _allStepNum);
            var _perDelay = _allTime / _allStepNum;
            animateStepAction(
                0,
                _perDelay,
                _allStepNum,
                () => {
                    zoom(_speed);
                },
                () => {
                    // console.log('....')
                    callback();
                }
            );
        }

        /**
         * API - 
         */
        function getNodes() {
            return nodeViewList;
        }

        /**
         * API - 
         */
        function getLines() {
            return lineViewList;
        }

        /**
         * API - 
         */
        function onNodeExpandEvent(node, e) {
            console.log('onNodeExpand:', node);
            if (props.onNodeExpand) {
                props.onNodeExpand(node, e);
            }
        }
        /**
         * API - 
         */
        function onNodeCollapseEvent(node, e) {
            console.log('onNodeCollapse:', node);
            if (props.onNodeCollapse) {
                props.onNodeCollapse(node, e);
            }
        }

        /**
         * API - 
         */
        function getGraphJsonData() {
            var _nodes = [];
            var _links = [];
            graphData.nodes.forEach((thisNode) => {
                SeeksRGUtils.transNodeToJson(thisNode, _nodes);
            });
            graphData.lines.forEach((thisLine) => {
                SeeksRGUtils.transLineToJson(thisLine, _links);
            });
            return {
                rootId: graphData.rootNode ? graphData.rootNode.id : '',
                nodes: _nodes,
                links: _links
            };
        }

        /**
         * API - 
         */
        function getGraphJsonOptions() {
            return seeksRGStore.getOptions();
        }

        /**
         * API - 
         */
        function printGraphJsonData() {
            console.log('graph options:', JSON.stringify(getGraphJsonOptions()));
            console.log('graph json data:', JSON.stringify(getGraphJsonData()));
        }

        /**
         * GraphSetting.checkedLineId
         */
        function updateGraphSetting(value) {
            graphSettingWrap.graphSetting = value;
        }

        /**
         * 
         */
        function updateLine(seekId, value) {
            lineViewList.forEach((element) => {
                if (element.seeks_id === seekId) {
                    element = value;
                }
            });
        }

        function updateNode(nodeId, value) {
            nodeViewList.forEach((element) => {
                if (element.id === nodeId) {
                    element = value;
                }
            });
        }

        // API - 
        provide('onDownloadExcel', props.onDownloadExcel);
        provide('refresh', refresh);
        provide('printGraphJsonData', printGraphJsonData);
        provide('onNodeExpandEvent', onNodeExpandEvent);
        provide('onNodeCollapseEvent', onNodeCollapseEvent);
        provide('getLineArrow', getLineArrow);

        provide('updateGraphSetting', updateGraphSetting);
        provide('updateLine', updateLine);
        provide('updateNode', updateNode);

        provide('zoom', zoom);

        // API - ref
        expose({
            setOptions,
            getPositionOfCanvas,
            getNodePositionTop,
            getNodePositionLeft,
            appendJsonData,
            setJsonData,
            refresh,
            getNodeById,
            removeNodeById,
            downloadAsImage,
            querySearchAsync,
            focusRootNode,
            focusNodeById,
            getNodes,
            getLines,
            onNodeExpandEvent,
            onNodeCollapseEvent,
            getGraphJsonData,
            getGraphJsonOptions,
            printGraphJsonData
        });

        return {
            graphSettingWrap,
            canvasSizeAndPosition,
            onRGNodeClick,
            onRGLineClick,
            moveDebugPanel,
            mouseListener,
            allLineColors,
            onDragStart,
            onDragEnd,
            isShowZoomCenter,
            rgCanvas,
            seeksRelationGraph,
            seeksRGCanvas,
            nodeViewList,
            lineViewList
        };
    }
});
</script>

