                </div>
                <div :class="{ 'c-mb-button-on': graphSetting.defaultLineShape === 6 }" class="c-mb-button c-mb-button-c" style="width: 50px" @click="changeLineShape(6)">
                    <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-lianjieliu"></use></svg>
