                    <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-lianjie_connecting5"></use></svg>
                    <span class="c-mb-text"></span>
                </div>
            </div>
        </div>
        <div
            v-if="graphSetting.isNeedShowAutoLayoutButton"
            :title="graphSetting.autoLayouting ? '' : ''"
            :class="{ 'c-mb-button-on': graphSetting.autoLayouting }"
            class="c-mb-button"
            @click="toggleAutoLayout"
        >
            <svg v-if="!graphSetting.autoLayouting" class="rg-icon" aria-hidden="true"><use xlink:href="#icon-zidong"></use></svg>
            <svg v-else class="c-loading-icon rg-icon" aria-hidden="true"><use xlink:href="#icon-lianjiezhong"></use></svg>
            <span class="c-mb-text"></span>
        </div>
        <div class="c-mb-button" @click="refresh">
            <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-ico_reset"></use></svg>
            <span class="c-mb-text"></span>
        </div>
        <div class="c-mb-button">
            <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-ziyuan"></use></svg>
            <span class="c-mb-text"></span>
            <div :style="{ width: downloadPanelWidth + 'px', 'margin-left': downloadPanelWidth * -1 + 'px' }" class="c-mb-child-panel">
                <div class="c-mb-button c-mb-button-c" style="width: 50px" @click="$parent.downloadAsImage('png')">
                    <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-tupian"></use></svg>
                    <span class="c-mb-text">PNG</span>
                </div>
                <div class="c-mb-button c-mb-button-c" style="width: 50px" @click="$parent.downloadAsImage('jpg')">
                    <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-tupian"></use></svg>
                    <span class="c-mb-text">JPG</span>
                </div>
                <div v-if="typeof $parent.onDownloadExcel === 'function'" class="c-mb-button c-mb-button-c" style="width: 50px" @click="$parent.onDownloadExcel()">
                    <svg class="rg-icon" aria-hidden="true"><use xlink:href="#icon-ziyuan"></use></svg>
                    <span class="c-mb-text">Excel</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent, onMounted, ref, inject, reactive } from 'vue';
import SeeksRGLayouters from './core4vue/SeeksRGLayouters';

export default defineComponent({
    name: 'GraphMiniToolBar',
    props: {
        graphSetting: {
            mustUseProp: true,
            default: () => {
                return {};
            },
            type: Object
        }
    },
    setup(props) {
        const height = ref(275);
        const hits = ref(0);
        const downloadPanelWidth = ref(106);

        const graphSetting = reactive(props.graphSetting);
        const refreshFun = inject('refresh');
        const onDownloadExcel = inject('onDownloadExcel');
        const printGraphJsonDataParent = inject('printGraphJsonData');
        const updateGraphSetting = inject('updateGraphSetting');
        const zoom = inject('zoom');

        onMounted(() => {
            if (onDownloadExcel !== null) {
                downloadPanelWidth.value += 50;
            }
            if (props.graphSetting.layouts.length > 1) {
                height.value -= 40;
            }
        });

        function refresh() {
            refreshFun();
        }

        function switchLayout(layoutConfig) {
            if (window.SeeksGraphDebug) console.log('change layout:', layoutConfig);
            SeeksRGLayouters.switchLayout(layoutConfig, props.graphSetting);
            refresh();
        }

        function toggleAutoLayout() {
            // console.log('props.graphSetting.autoLayouting', props.graphSetting.autoLayouting)
            graphSetting.autoLayouting = !props.graphSetting.autoLayouting;
            updateGraphSetting(graphSetting);
            if (props.graphSetting.autoLayouting) {
                if (!props.graphSetting.layouter.autoLayout) {
                    console.log('');
                } else {
                    props.graphSetting.layouter.autoLayout(true);
                }
            } else {
                if (!props.graphSetting.layouter.stop) {
                    console.log('stop');
                } else {
                    props.graphSetting.layouter.stop();
                }
            }
        }

        function printGraphJsonData() {
            hits.value++;
            setTimeout(() => {
                if (hits.value > 0) hits.value--;
            }, 2000);
            if (hits.value > 5) {
                hits.value = 0;
                printGraphJsonDataParent();
            }
        }

        function toggleFullscreen() {
            graphSetting.fullscreen = !props.graphSetting.fullscreen;
            updateGraphSetting(graphSetting);
        }

        function changeJunctionPoint(value) {
            graphSetting.defaultJunctionPoint = value;
            updateGraphSetting(graphSetting);
        }

        function changeLineShape(value) {
            graphSetting.defaultLineShape = value;
            updateGraphSetting(graphSetting);
        }

        return {
            downloadPanelWidth,
            refresh,
            switchLayout,
            toggleAutoLayout,
            printGraphJsonData,
            toggleFullscreen,
            changeJunctionPoint,
            changeLineShape,
            zoom
        };
    }
});
</script>

