<template>
    <div ref="miniView" class="c-mini-graph">
        <div :style="{ width: 100 * zoom + 'px', height: ((graphSetting.canvasNVInfo.height * 100) / graphSetting.canvasNVInfo.width) * zoom + 'px' }" class="c-mini-canvas">
            <template v-for="thisNode in $parent.nodeViewList">
                <div
                    v-if="isAllowShowNode(thisNode)"
                    :key="thisNode.id"
                    :style="{ 'margin-left': ((thisNode.x * 100) / graphSetting.canvasSize.width) * zoom + 'px', 'margin-top': ((thisNode.y * 100) / graphSetting.canvasSize.width) * zoom + 'px' }"
                    class="c-mini-node"
                />
            </template>
        </div>
        <div :style="getPositionData()" class="c-mini-view">
            <i class="el-icon-view" />
        </div>
    </div>
</template>

<script>
import { defineComponent, ref } from 'vue';
import SeeksGraphMath from './core4vue/SeeksGraphMath';

export default defineComponent({
    name: 'GraphMiniView',
    props: {
        graphSetting: {
            mustUseProp: true,
            default: () => {
                return {};
            },
            type: Object
        }
    },
    setup(props) {
        let zoom = ref(1);

        function getPositionData() {
            var _c_width = 100;
            var _r = _c_width / props.graphSetting.canvasNVInfo.width;
            var _width = props.graphSetting.viewNVInfo.width * _r;
            var _height = props.graphSetting.viewNVInfo.height * _r;
            var _view_x = (props.graphSetting.viewNVInfo.x - props.graphSetting.canvasNVInfo.x) * _r;
            var _view_y = (props.graphSetting.viewNVInfo.y - props.graphSetting.canvasNVInfo.y) * _r;
            if (_width > 100) {
                _height = (_height * 100) / _width;
                _view_x = (_view_x * 100) / _width;
                _view_y = (_view_y * 100) / _width;
                zoom.value = 100 / _width;
                _width = 100;
            } else {
                zoom.value = 1;
            }
            // console.log('Mini View style:', _view_center_x, _canvas_center_x)
            var style = {
                width: _width + 'px',
                height: _height + 'px',
                'margin-left': _view_x + 'px',
                'margin-top': _view_y + 'px'
            };
            return style;
        }

        function isAllowShowNode(nodeData) {
            return SeeksGraphMath.isAllowShowNode(nodeData);
        }

        return {
            zoom,
            getPositionData,
            isAllowShowNode
        };
    }
});
</script>

<style scoped>
.c-mini-graph {
    height: 100px;
    width: 100px;
    position: absolute;
    margin-left: 60px;
    margin-top: 100px;
    z-index: 999;
}
.c-fixedLayout {
    position: fixed;
    top: 100px;
}
.c-mini-canvas {
    background-color: #aacbff;
    border: #7ba8ff solid 1px;
    opacity: 0.8;
    position: absolute;
}
.c-mini-view {
    background-color: #f5a565;
    border: #c03639 solid 1px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 14px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
}
.c-mini-node {
    position: absolute;
    width: 2px;
    height: 2px;
    background-color: #000000;
    border-radius: 1px;
}
</style>
